.Form{
  display: flex;
  align-items: center;
  justify-content: center;
}

.addForm{
  width: 400px;
  height: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 2fr 1fr 1fr;
}

.postText{
  grid-column-start: span 2;
}

label{
  margin-right: 10px;
}

.loaderContainer{
  display: flex;
  justify-content: center;
}
